import { Order } from '../Order.js'
export default {
  props: {
    text: String
  },
  created () {
    Order.$on('Loading', (message) => {
      if(message === 'show'){
        this.show = true
      }
      else{
        this.show = false
      }
    })
  },
  data () {
    return {
      show: false
    }
  }
}



