export default {
  props: {
    required: {
      type: Boolean,
      default: false
    },
    placeholder: String,
    results: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    clear () {
      //输入框X号点击事件
      this.currentValue = ''
      this.isFocus = true
      this.setFocus()
      if (!this.isFixed) {
        this.isFixed = true
      }
    },
    cancel () {
      //点击取消按钮
      this.isCancel = true
      this.currentValue = ''
      this.isFixed = false
      this.$emit('on-cancel')
    },
    handleResultClick (item) {
      this.$emit('result-click', item) // just for compatibility
      this.$emit('on-result-click', item)
      this.isCancel = true
      this.isFixed = false
    },
    touch () {
      console.log("touch")
      this.isCancel = false
      this.isFixed = true
      this.$emit('on-touch')
    },
    setFocus () {
      console.log("touch")
      this.$refs.input.focus()
    },
    onFocus () {
      this.isFocus = true
      this.touch()
    },
    onBlur () {
      console.log("onBlur")
      this.isFocus = false
      this.isCancel = true
      this.isFixed = false
      this.currentValue = ''
    }
  },
  data () {
    return {
      currentValue: '',
      isCancel: true,
      isFocus: false,
      isFixed: false,
    }
  },
  watch: {
    isFixed (val) {
      if (val === true) {
        this.setFocus()
        this.isFocus = true
      } else {}
    },
  }
}



