
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import Search from './panel/Search'
import TitleBar from './bar/Title'
import { post,globalData} from "./method.js" 
//引入图片资源
const $tiangongyuanyuan = require('../assets/tiangongyuanyuan.png'),
      $xinxifabu        = require('../assets/xinxifabu.png'),
      $youjian          = require('../assets/youjian.png'),
      $bangongxitong    = require('../assets/bangongxitong.png');
export default {
  components: {
    Search,
    Checker,
    CheckerItem,
    TitleBar
  },
  methods: {
    onIndexChange: function(index) {
      this.index = index
    },
    openStart:function(url,special){ //判断以何种方式打开应用
      switch(special){
        case 'open':this.openApp();break; //启动应用
        case 'url':window.location.href=url;break; //跳转到Url
      }
    },
    openApp: function () { //打开应用
      const app1 = {
        "type":2,
        "sopid":"com.vrv.linkDood",
        "pkgpath":"com.vrv.linkDood-1.0.45.sop",
        "scheme":"linkdood:showlinkdood?id="+this.idCard,
        "name":"linkdood"
      };
      //向9999端口发送Post请求打开应用
      post("http://localhost:9999/open",app1,function(date){
        console.log(date)
      });
    },
    installApp: function(item,key){
      globalData.appList[key].exist = true
    }
  },
  data () {
    return {
      select: 'all',
      appList: globalData.appList,
    }
  },
  computed: {
    //筛选应用
    classification: function () {
      const _this = this;
      if(_this.select === "all"){
        return globalData.appList
      }
      else{
        const newList ={}
        for(let item in globalData.appList){
          //判断应用列表的类型是否和选择的类型一致
          if(globalData.appList[item].type === _this.select){
            newList[item] = globalData.appList[item]
          }
        }
        return newList
      }
    }
  }
}



