import { Order } from '../Order.js'
export default {
  data () {
    return {
      show: false,
      text: "..."
    }
  },
  created () {
    Order.$on('Toast', (message) => {
      this.text = message
      this.show = true
      const _this = this
      function hide(){
        _this.show = false
      }
      setTimeout(hide,2000)
    })
  },
}



