export default {
  props: {
    index: String,
  },  
  methods: {
    //通知标签点击事件
    noticeTabClick() {
      this.tabIndex='通知'
      window.location.href="#/Main"
    },
    //我的应用标签点击事件
    appTabClick () {
      this.tabIndex='我的应用'
      window.location.href="#/App"
    },
    //增加应用事件
    addApp () {
      this.tabIndex = '应用商店'
      window.location.href="#/"
      //将标题栏右边按钮隐藏
      this.rightIcon = {isActive:false}
      //将标题栏左边按钮显示
      this.leftIcon = {isActive:true,ico:"&#xe601;"}
    },
    comeBack () {
      this.tabIndex = '我的应用'
      //将标题栏右边按钮隐藏
      this.leftIcon = {isActive:false}
      //将标题栏左边按钮显示
      this.rightIcon = {isActive:true,ico:"&#xe601;"}
      window.location.href="#/"
    },
    contactsTabClick () {
      this.tabIndex='通讯录'
      this.rightIcon = {isActive:false}
      window.location.href="#/Contacts"
    },
    meTabClick () {
      this.tabIndex='我'
      this.rightIcon = {isActive:false}
      window.location.href="#/Me"
    }
  },
}



